// @mui
import { useEffect, useState } from 'react';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
// components
import { useSettingsContext } from 'src/components/settings';
import DataTable from 'react-data-table-component';
import { Box, Button, IconButton, InputAdornment, Menu, MenuItem, TextField } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import axios, { endpoints } from 'src/utils/axios';
import { constants } from 'src/utils/constant';
import { useSnackbar } from 'src/components/snackbar';
import { decryptToken, color } from 'src/utils/common';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { SingleInputDateRangeField } from '@mui/x-date-pickers-pro/SingleInputDateRangeField';
import dayjs from 'dayjs';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { useAuthContext } from 'src/auth/hooks';
// ----------------------------------------------------------------------

export default function ReceiptView() {
  const settings = useSettingsContext();
  const { enqueueSnackbar } = useSnackbar();
  const [searchQuery, setSearchQuery] = useState('');
  const [tempList, setTempList] = useState([])
  const [isLoading, setIsLoading] = useState(false);
  const [value, setValue] = useState(() => [
    dayjs('2022-04-17'),
    dayjs('2022-04-21'),
  ]);
  const { user } = useAuthContext();
  const [data, setData] = useState([]);
  // For Actions Dropdown
  const [anchorEl, setAnchorEl] = useState(null);
  const openMenu = Boolean(anchorEl);
  // Temporary State
  const [tempData, setTempData] = useState({});
  const [openDialog, setOpenDialog] = useState(false);


  const fetchData = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.receipt.list, {}, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setIsLoading(false);
        console.log(response.data.data);
        setData(response.data.data);
        setTempList(response.data.data);
      };

    } catch (err) {
      enqueueSnackbar('Failed to fetch records!', { variant: 'error' });
      setIsLoading(false);
    }
  };

  useEffect(() => {
    setIsLoading(true);
    fetchData();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const columns = [
    {
      name: 'User Name',
      selector: (row) => {
        if (row.partyType === 'CUSTOMER' || row.partyType === 'VENDOR') {
          return row.userName || 'N/A';
        }
        return row.category || 'N/A';
      },
      sortable: true,
    },
    {
      name: 'Invoice Number',
      selector: (row) => row?.invoiceNumber,
      sortable: true,
    },
    {
      name: 'Amount Paid',
      selector: (row) => row?.amount?.toFixed(2),
      sortable: true,
    },
    {
      name: 'Payment Mode',
      selector: (row) => row?.paymentId?.paymentMode,
      sortable: true,
    },
    {
      name: 'Payment Status',
      selector: (row) => row?.paymentId?.paymentStatus,
      sortable: true,
    },
    {
      name: 'Party Type',
      selector: (row) => row?.partyType,
      sortable: true,
    },
    {
      name: 'Payment Date',
      selector: (row) => new Date(row?.createdAt || null).toLocaleDateString('en-GB', { day: '2-digit', month: 'long', year: 'numeric' }),
      sortable: true,
    },
    {
      name: 'Actions',
      width: "130px",
      cell: (row) => (
        <>
          <Button
            disabled={!user?.permissions?.receipt?.u}
            onClick={(event) => {
              setAnchorEl(event.currentTarget);
              setTempData(row)
            }} style={{ color: color.actionButton }} variant='outlined' endIcon={<ArrowDropDownIcon />}>Action</Button>
          <Menu
            id="basic-menu"
            anchorEl={anchorEl}
            open={openMenu}
            onClose={() => {
              setAnchorEl(null);
              setTempData({});
            }}
            MenuListProps={{
              'aria-labelledby': 'basic-button',
            }}
          >
            <MenuItem onClick={() => {
            }}>View</MenuItem>
            <MenuItem onClick={() => {
            }}>Print</MenuItem>
            <MenuItem onClick={() => {
            }}>Share via Whatsapp</MenuItem>
            <MenuItem onClick={() => {
            }}>Share via Email</MenuItem>
            <MenuItem onClick={() => {
            }}>Delete</MenuItem>

          </Menu>
        </>
      ),
    },
  ];

  const filteredRecords = data.filter(filteredData => {
    const invoiceNumberMatches = filteredData?.invoiceNumber?.toLowerCase().includes(searchQuery?.toLowerCase());
    const customerNameMatches = filteredData?.userName?.toLowerCase().includes(searchQuery?.toLowerCase()); // Assuming 'customerName' is the key for the customer name in your data
    const categoryNames = filteredData?.category?.toLowerCase().includes(searchQuery?.toLowerCase());
    return invoiceNumberMatches || customerNameMatches || categoryNames;
  });


  const [startDate, setStartDate] = useState(dayjs().startOf('month'));
  const [endDate, setEndDate] = useState(dayjs().endOf('month'));

  const handleStartDateChange = (date) => {
    setStartDate(date);
  };

  const handleEndDateChange = (date) => {
    setEndDate(date);
  };

  useEffect(() => {
    if (startDate && endDate) {
      const filteredSales = tempList.filter((sale) => {
        const saleDate = dayjs(sale.createdAt);
        console.log(startDate, endDate)
        return saleDate.isBetween(startDate, endDate, null, '[]');
      });
      setData(filteredSales);
    } else {
      setData(tempList);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [startDate, endDate])

  return (
    <Container maxWidth={settings.themeStretch ? false : 'xl'}>
      <Typography variant="h4"> Receipts </Typography>
      <div style={{ display: 'flex', justifyContent: 'start', alignItems: 'center', marginTop: '20px', gap: '10px' }}>
        <TextField
          size="small"
          onChange={(e) => setSearchQuery(e.target.value)}
          type="text"
          placeholder="Search by Inv/Name"
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SearchIcon />
              </InputAdornment>
            ),
          }}
        />
        <div>
          <LocalizationProvider dateAdapter={AdapterDayjs} >
            <Box sx={{ display: 'flex', flexDirection: 'row', gap: '10px', alignItems: 'center' }}>
              <DatePicker
                label="From"
                format='DD/MM/YYYY'
                value={startDate}
                slotProps={{ textField: { size: 'small' } }}
                onChange={handleStartDateChange}
                renderInput={(props) => <TextField {...props} />}
              />
              <Typography>-</Typography>
              <DatePicker
                label="To"
                format='DD/MM/YYYY'
                value={endDate}
                slotProps={{ textField: { size: 'small' } }}
                onChange={handleEndDateChange}
                renderInput={(props) => <TextField {...props} />}
              />
            </Box>
          </LocalizationProvider>
        </div>
      </div>
      <div style={{ marginTop: "20px" }}>
        <DataTable
          columns={columns}
          data={filteredRecords}
          pagination
          progressPending={isLoading}
        />
      </div>

    </Container>
  );
}
